.header {
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  margin: 0 auto;
}
.fa-paw {
  color: rgb(255, 94, 0);
}
@media (min-width: 600px) {
  .App {
    width: 80%;
    margin: 0 auto;
  }
}
.card {
  /* background-color: red; */
  width: 90%;
  height: 80vh;
  margin: 0 auto;
  object-fit: cover;
  border-radius: 16px;
  overflow: hidden;
  position: relative;
}
.card img {
  width: 100%;
  height: 80vh;
  object-fit: contain;
}
.info {
  border-radius: 16px;
  position: absolute;
  z-index: 1;
  bottom: 0;
  padding: 0 24px;
  background-color: rgba(0, 0, 0, 0.103);
  backdrop-filter: blur(5px);
  width: calc(100% - 48px);
}
.info p {
  color: rgb(255, 60, 0);
  word-wrap: break-word;
}
.info button {
  position: absolute;
  right: 24px;
  bottom: 12px;
  color: white;
}
.info button:hover {
  color: rgb(255, 94, 0);
}

button {
  background-color: rgba(0, 0, 0, 0.075);
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  backdrop-filter: blur(10px);
  transition: 0.05s;
  cursor: pointer;
}
.about {
  height: 100%;
  position: relative;
  text-align: center;
}
.about-inner {
  width: 100px;
  padding: 0.2rem 2rem;
  background-color: white;
  box-shadow: 0 0 30px 10px rgba(0, 0, 0, 0.07);
  border-radius: 12px;
  position: absolute;
  z-index: 1;
  top: calc(100% + 0px);
  right: 0;
  transition: 0.5s;
  display: none;
}
.about:hover .about-inner {
  display: block;
  transition: 0.5s;
  transform: translateY(10px);
}
.tooltip-box {
  width: 30px;
  height: 30px;
  position: absolute;
  top: -5px;
  background-color: white;
  right: 15px;
  transform: rotateZ(45deg);
}
button:hover .fa {
  color: rgb(255, 60, 0);
  transition: 0.1s;
}
.about p {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.55rem;
  color: rgb(161, 159, 173);
}
.about h3 {
  color: rgb(57, 57, 66);
}
